@media screen and (max-width: $maxwidth-first) {
  html,
  body {
  overflow-x: hidden;
}
.header {
  width: 100%;
  height: 840px;
  background: #56CCF2;
}

.heading {
  left: 10px;
  top: 300px;
  width: 350px;

  &__name {
    font-size: 54px;
    margin-top: 100px;
  }
  &__descrip {
    font-size: 18px;
    margin-bottom: 20px;

  }
  &__button {
    width: 150px;
    height: 50px;
    margin: 0 auto;
  }
}
}


@media screen and (max-width: $maxwidth-first) {

.header__nav {
  left: 90px;
}
.nav__list {
  flex-direction: column;
  align-items: center;
}
.nav__item {
  margin-top: 10px;
}
.nav__item_title {
  padding: 0;
  font-size: 40px;
}
.nav__link {
  font-weight: bold;
  font-size: 24px;
  &::after {
    width: 80px;
  }
}
}

@media screen and (max-width: $maxwidth-first) {
.photo {
  margin-top: 30px;
  &__title,
  &__text {
    display: none;
  }
}
.photo__img {
  object-fit: cover;
  height: 350px;
}
.button {
  margin: 317px 250px;
}
}


@media screen and (max-width: $maxwidth-first) {
.section {
  padding-top: 40px;
  }
}

@media screen and (max-width: $maxwidth-first) {
.inform {
  display: flex;
  width: 250px;
  }
}

@media screen and (max-width: $maxwidth-first) {
.cards {
  display: flex;
  flex-direction: column;
  width: 300px;
  &__item {
    margin-bottom: 30px;

  }
}
}


@media screen and (max-width: $maxwidth-first) {
.process {
  justify-content: center;
  flex-direction: column;
  &__title {
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
  }
  &__text {
    padding-left: 0;
  }
  &__heading {
    padding-left: 0;
  }
}
}

@media screen and (max-width: $maxwidth-first) {
.block__cards_one{
  display: inline-block;
  margin-top: 20px;
  margin-left: 0;
}
.block__cards_two {
  display: inline-block;
  margin-left: 0;
  margin-top: 130px;
}
}

@media screen and (max-width: $maxwidth-first) {
.slider_rotate {
  display: none;
  }
}

@media screen and (max-width: $maxwidth-first) {
.features {
  width: 1300px;
  height: 1100px;
  }
  .features__inform {
  display: block;
  margin-left: 40px;
  }
}
@media screen and (max-width: $maxwidth-first) {
.testimonials__title {
  margin-left: 40px;
  }
.testimonials__heading {
  margin-top: 20px;
  margin-left: -160px;
}
.people {
  flex-direction: column;
  &__item {
    width: 270px;
  }
}
}



